import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { setTransaksiServices } from "../../../actions/services_action.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { PageSettings } from "../../../config/page-settings.js";
import HeadServices from "./HeadServices.jsx";
const maptostate = (state) => {
    return {
      dataservices: state.services.setTransaksiServices,
    };
  };
class FormTransaksiServces extends React.Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        footer: "Total"
        },

        {
          dataField: "berat",
          text: "Berat",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0).toFixed(1),
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "total",
          text: "Total Rp",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "deskripsi",
          text: "Deskripsi",
          footer: "",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }


  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      // console.log(id);
      if (result.isConfirmed) {
        let data = JSON.parse(
          localStorage.getItem("data_barang_services")
        );
        data.splice(id, 1);
        localStorage.setItem("data_barang_services", JSON.stringify(data));
        this.props.dispatch(setTransaksiServices(data));
        // deleteLocalItemBarcode("data_barang_hancur_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
        this.setState({
          berhasil : true,
        });
      }
    });
  }

  componentWillUnmount() {
    localStorage.setItem("data_customer_services_tmp", JSON.stringify([]));
    localStorage.setItem("data_barang_services", JSON.stringify([]));
  }
  componentDidMount() {
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
}

  handleSubmit(data) {}

    render() {
    return (
      <div>
        <Link to="/SubMenuService">
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <Panel>
          <PanelHeader>Transaksi Services</PanelHeader>
          <br />
          {/* Transaksi Services */}
          <div className="container">
            <HeadServices
              isLoading={this.state.isLoading}
              columns={this.state.columns}
            />
          </div>
          <br />
          {/* End Tambah Transaksi Services  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate,null)(FormTransaksiServces);