import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { getDataSales } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import { getData } from "../../../config/axios";
import { Gramasi } from "../../../Validasi/normalize";
import ValidasiTransaksiService from "../../../Validasi/Services/ValidasiTransaksiService";
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});

class ModalTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      harga_jual: true,
      berat_jual: true,
      hargapergram: true,
      ongkos: true,
      total: true,
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    setTimeout(() => {
      document.getElementById("nama_barang").focus();
    }, 100);
  }
  getBarcode(e) {
    let cek =
      JSON.parse(localStorage.getItem("data_barang_penjualan_tmp")) || [];
    let hasil_cek = cek.findIndex((hasil) => hasil.kode_barcode === e);
    if (hasil_cek === -1) {
      getData("barang/get/by-kode-barcode/" + e)
        .then((res) => {
          if (res.data.length === 0) {
            ToastNotification("info", "Data Yang Dicari Tidak Ada");
            this.props.change("nama_barang", "");
            this.props.change("nama_atribut", "");
            this.props.change("berat_atribut", "");
            this.props.change("harga_atribut", "");
            this.props.change("berat", "");
          } else {
            res.data.forEach((list, index) => {
              this.props.change("nama_barang", list.nama_barang);
              this.props.change("nama_atribut", list.nama_atribut);
              this.props.change("berat_atribut", list.berat_atribut);
              this.props.change("harga_atribut", list.harga_atribut);
              this.props.change("berat", list.berat);
              this.setState({
                harga_jual: false,
                berat_jual: false,
                hargapergram: false,
                ongkos: false,
              });
            });
          }
        })
        .catch((err) => {
          ToastNotification("info", "Data Yang Dicari Tidak Ada");
        });
    } else {
      ToastNotification("info", "Barang Sudah Ada Di Tabel");
    }
  }
  hitungTotal() {
    this.props.change("total", this.props.total);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-12 col-md-6">
            <Field
              tabIndex="1"
              id="nama_barang"
              name="nama_barang"
              label="Nama Barang"
              type="text"
              placeholder="Masukan Nama Barang"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-6">
            <Field
              tabIndex="2"
              name="berat"
              label="Berat Barang"
              onChange={this.hitungTotal()}
              type="text"
              normalize={Gramasi}
              placeholder="Masukan Berat Barang"
              component={ReanderField}
            />
          </div>
        </div>
        <div className="row">
          <div className="col-12 col-md-6">
            <Field
              tabIndex="3"
              name="jumlah"
              label="Jumlah"
              type="number"
              readOnly={true}
              defaultValue="1"
              onChange={this.hitungTotal()}
              placeholder="Masukan Jumlah"
              component={ReanderField}
            />
          </div>
          <div className="col-12 col-md-6">
            <Field
              tabIndex="4"
              name="ongkos"
              label="Ongkos"
              type="tel"
              {...currencyMask}
              placeholder="Masukan Ongkos"
              component={ReanderField}
            />
          </div>
        </div>
        <div className="row">
          <div className="col-12 col-md-12">
            <Field
              name="total"
              label="Total"
              type="tel"
              readOnly={true}
              placeholder="Masukan Total"
              component={ReanderField}
              {...currencyMask}
            />
          </div>

          <div className="col-12 col-md-12">
            <Field
              tabIndex="5"
              name="deskripsi"
              label="Deskripsi"
              type="textarea"
              placeholder="Masukan Deskripsi"
              component={ReanderField}
            />
          </div>
        </div>

        <div className="row">
          <div className="col-6"></div>
          <div className="col-6">
            <button
              tabIndex="6"
              className="btn btn-primary btn-block"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

// ModalTambahBarang = reduxForm({
//   form: "ModalTambahBarang",
//   enableReinitialize: true,
//   validate: ValidasiMasterDataHadiah,
// })(ModalTambahBarang);
// export default connect(maptostate, null)(ModalTambahBarang);

ModalTambahBarang = reduxForm({
  form: "ModalTambahBarang",
  enableReinitialize: true,
  validate: ValidasiTransaksiService,
})(ModalTambahBarang);

export default connect((state) => {
  const selector = formValueSelector("ModalTambahBarang"); // <-- same as form name
  const { ongkos, jumlah } = selector(state, "ongkos", "jumlah");
  return {
    initialValues: {
      jumlah: "1",
    },
    total: parseFloat(ongkos || 0) * parseFloat(jumlah || 0) || 0,
    DataSales: state.datamaster.getDataSales,
  };
})(ModalTambahBarang);
