const ValidasiTransaksiService = (value) => {
    const errors = {};
    if (!value.nama_barang) {
        errors.nama_barang = "Nama Barang Tidak Boleh Kosong";
    }
    if (!value.berat) {
        errors.berat = "Berat Tidak Boleh Kosong";
    }
    if (!value.ongkos) {
        errors.ongkos = "Ongkos Tidak Boleh Kosong";
    }

    return errors;
}

export default ValidasiTransaksiService;


