import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import {
  getDataSales,
  hideModal,
  showModal,
} from "../../../actions/datamaster_action";
import { setTransaksiServices } from "../../../actions/services_action";
import { getItem } from "../../../components/helpers/function";
import ModalGlobal from "../../../components/helpers/ModalGlobal";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { Panel } from "../../../components/panel/panel";
import { getData, postData } from "../../../config/axios";
import ValidasiDataUsers from "../../../Validasi/ManageUsers/ValidasiDataUsers";
// import { NumberOnly } from "../../../Validasi/normalize";
import ModalDataCustomer from "./ModalDataCustomer";
import ModalPembayaran from "./ModalPembayaran";
import ModalTambahBarang from "./ModalTambahBarang";
const maptostate = (state) => {
  return {
    setTransaksiServices: state.services.setTransaksiServices,
    DataSales: state.datamaster.getDataSales,
    hideModal: state.datamaster.modalDialog,
  };
};

class HeadServices extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isLoadingBatal: false,
      isLoadingDataCustomer: false,
      FormModal: "",
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
  }
  getBarcode(e) {
    getData("barang/get/by-kode-barcode/" + e)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Dicari Tidak Ada");
          this.props.change("nama_barang", "");
          this.props.change("nama_atribut", "");
          this.props.change("berat_atribut", "");
          this.props.change("harga_atribut", "");
        } else {
          // console.log(res.data);
          res.data.forEach((list, index) => {
            this.props.change("nama_barang", list.nama_barang);
            this.props.change("nama_atribut", list.nama_atribut);
            this.props.change("berat_atribut", list.berat_atribut);
            this.props.change("harga_atribut", list.harga_atribut);
          });
        }
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Dicari Tidak Ada");
      });
  }

  handleSubmit(data) {
    let hasil = {
      alamat_customer: data.alamat_customer || "-",
      jenis_pelanggan: data.jenis_pelanggan || "NONMEMBER",
      kode_customer: data.jenis_pelanggan === "NONMEMBER" ? "NONMEMBER" : data.kode_customer,
      kode_sales: data.kode_sales,
      nama_customer: data.nama_customer || "REGULER",
      no_hp: data.no_hp || "-"
    }
    this.setState({
      isLoadingDataCustomer: true,
    });
    localStorage.setItem("data_customer_services_tmp", JSON.stringify(hasil));
    setTimeout(() => {
      this.setState({
        isLoadingDataCustomer: false,
      });
      this.props.dispatch(hideModal());
    }, 500);
  }
  simpanBarang(data) {
    data = {
      nama_barang: data.nama_barang.toUpperCase() || "-",
      berat: parseFloat(data.berat) || 0,
      jumlah: parseFloat(data.jumlah) || 0,
      ongkos: parseInt(data.ongkos) || 0,
      total: parseInt(data.total) || 0,
      deskripsi: data.deskripsi === undefined ? data.nama_barang.toUpperCase() : data.deskripsi.toUpperCase() || "-",
    };
    let cek = JSON.parse(localStorage.getItem("data_barang_services")) || [];
    this.props.dispatch(reset("ModalTambahBarang"));
    document.getElementById("nama_barang").focus();
    cek.push(data);
    localStorage.setItem("data_barang_services", JSON.stringify(cek));
    this.props.dispatch(setTransaksiServices(cek));
    this.props.dispatch(hideModal());
    ToastNotification("success", "Barang Berhasil Ditambahkan");
    this.props.change("nama_barang", "");
  }

  bayarSekarang(data) {
    if(getItem("trx_pemabayaran_services").length <= 0){
      ToastNotification("info", "Mohon Tambahkan metode pembayaran minimal 1..");
      return false
    }
    this.setState({
      isLoading: true,
    });
    // let data_pembayaran = {
    //   jenis: data.jenis,
    //   jumlah_rp: parseInt(data.cash) > parseInt(data.grand_total_pembayaran) ? Math.abs(parseInt(data.cash) - parseInt(NumberOnly(data.kembali))) : parseInt(data.cash),
    // };

    let pembayaran = getItem("trx_pemabayaran_services");
    

    let data_pembayaran = [];
    let total_pembayarn = 0;
    let jumlah_rp = 0;
    pembayaran.forEach((element) => {
      if (element.jenis === "CASH") {
        if (parseInt(element.nominal) <= Math.abs(parseInt(getItem("sisa_bayar_services").sisa_bayar))) {
          jumlah_rp = Math.abs(parseInt(getItem("sisa_bayar_services").sisa_bayar) - parseInt(element.nominal));
        } else {
          jumlah_rp = parseInt(element.nominal);
        }
        if (parseInt(element.nominal) >= Math.abs(parseInt(getItem("sisa_bayar_services").total_transaksi))) {
          jumlah_rp = parseInt(element.nominal) - Math.abs(parseInt(getItem("sisa_bayar_services").sisa_bayar));
        } else {
          jumlah_rp = parseInt(element.nominal);
        }
      } else {
        jumlah_rp = parseInt(element.nominal);
      }
      let row = {
        jenis: element.jenis,
        keterangan: element.keterangan,
        fee: element.jenis === "CREDIT" ? parseInt(element.fee) || 0 : 0,
        jumlah_rp : jumlah_rp
        // jumlah_rp: parseInt(element.nominal) >= parseInt(getItem('sisa_bayar_services').total_transaksi) && element.jenis ==="CASH" ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_services').sisa_bayar) : parseInt(element.nominal) > parseInt(getItem('sisa_bayar_services').sisa_bayar) && element.jenis ==="CASH" ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_services').sisa_bayar) : parseInt(element.nominal),
        // jumlah_rp: element.jenis === "CASH" ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_services').sisa_bayar) : parseInt(element.nominal),
        // jumlah_rp: element.nominal >= parseInt(getItem('sisa_bayar_services').total_transaksi) ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_services').sisa_bayar) : parseInt(element.nominal),
        
      };
      // console.log(parseInt(element.nominal));
      // console.log(parseInt(getItem('sisa_bayar_services').total_transaksi));
      total_pembayarn = element.nominal + total_pembayarn;
      data_pembayaran.push(row);
    });
     
  
    let data_customer = JSON.parse(
      localStorage.getItem("data_customer_services_tmp")
    );
    let data_barang_penjualan = JSON.parse(
      localStorage.getItem("data_barang_services")
    );

    let detail_barang = [];
    data_barang_penjualan.forEach((item, index) => {
      let row = {
        nama_barang: item.nama_barang,
        berat: item.berat,
        jumlah: item.jumlah,
        ongkos: item.ongkos,
        total_rp: item.total_rp,
        deskripsi: item.deskripsi,
      };
      detail_barang.push(row);
    });
    let data_services = {
      kode_sales: data_customer.kode_sales,
      nama_customer: data_customer.nama_customer,
      kode_member:
        data_customer.jenis_pelanggan === "NONMEMBER"
          ? data_customer.jenis_pelanggan
          : data_customer.kode_customer,
      alamat_customer: data_customer.alamat_customer,
      no_hp: data_customer.no_hp,
      total: parseInt(getItem('sisa_bayar_services').total_transaksi),
      dp_rp: parseInt(total_pembayarn) < parseInt(getItem('sisa_bayar_services').total_transaksi) ? parseInt(total_pembayarn) : parseInt(total_pembayarn) >= parseInt(getItem('sisa_bayar_services').total_transaksi) ? parseInt(getItem('sisa_bayar_services').total_transaksi) : 0,
      // dp_rp: parseInt(data.cash) > parseInt(data.grand_total_pembayaran) ? Math.abs(parseInt(data.cash) - parseInt(NumberOnly(data.kembali))) : parseInt(data.cash),
      detail_barang: detail_barang,
      pembayaran: data_pembayaran,
    };
    // return false
    postData("service/simpan", data_services)
      .then((res) => {
        this.setState({
          isLoading: false,
        });
        ToastNotification("success", "Transksi Service Berhasil");
let notaservices = [];
for (let index = 0; index < 2; index++) {
let feedback = "";
res.data.forEach((row, index) => {
let jml_alamat = row.n_alamat_customer.length;
feedback += `========================================\n`;
feedback += `             NOTA SERVICE               \n`;
feedback += `========================================
Tanggal          : ${row.tgl_system}
Kode Sales       : ${row.kode_sales}
Nama Customer    : ${row.nama_customer}
No Hp            : ${row.no_hp}
Alamat Customer  : ${row.n_alamat_customer.slice(0, 20)}
Total Service    : ${row.total_service.toLocaleString("kr-KO")}
`;
if (jml_alamat > 20) {
feedback += `      ${row.n_alamat_customer.slice(20,40)}
`;
}
if (jml_alamat > 40) {
feedback += `      ${row.n_alamat_customer.slice(40,60)}
`;
}
feedback += `========================================\n`;
feedback += `Keterangan\n`;
feedback += `========================================\n`;
let ongkos = 0;
let harga_nota = 0;
row.detail_barang.forEach((detail, index) => {
ongkos = ongkos + detail.ongkos;
let jml_des = detail.deskripsi.length;
harga_nota = harga_nota + detail.harga_nota;
feedback += `Nama Barang      : ${detail.nama_barang}\n`;
feedback += `Berat            : ${detail.berat.toFixed(3)}\n`;
feedback += `Jumlah           : ${detail.jumlah.toLocaleString("kr-KO")}\n`;
feedback += `Ongkos           : ${detail.ongkos.toLocaleString("kr-KO")}\n`;
feedback += `deskripsi        : ${detail.deskripsi.slice(0,20)}\n`;
if (jml_des > 20) {
feedback += `                   ${row.deskripsi.slice(20, 40)}`;
}
if (jml_des > 40) {
feedback += `                   ${row.deskripsi.slice(40, 60)}`;
}
feedback += `========================================\n`;
});
feedback += `Total Ongkos     : ${ongkos.toLocaleString("kr-KO")}\n`;
feedback += `========================================\n`;
feedback += `                               TTD      \n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                         (             )\n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                                        \n`
  
}
);
notaservices.push(feedback);
}

document.getElementById("myInput").value = notaservices.join("");
}).then(() => {
  const element = document.createElement("a");
  const file = new Blob([document.getElementById("myInput").value], {
    type: "text/plain;charset=utf-8",
  });
  element.href = URL.createObjectURL(file);
  element.download = "autoprint_service.txt";
  document.body.appendChild(element);
  element.click();
}).then(() => {
        this.props.dispatch(hideModal());
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        localStorage.setItem("data_customer_services_tmp", JSON.stringify([]));
        localStorage.setItem("data_barang_services", JSON.stringify([]));
        localStorage.setItem("sisa_bayar_services", JSON.stringify([]));
        localStorage.setItem("trx_pemabayaran_services", JSON.stringify([]));
        this.setState({
          isLoading: false,
        });
      })
      .then(() => {
        this.props.dispatch(setTransaksiServices([]));
      })
      .catch((err) => this.errorhandling(err));
    // localStorage.setItem("tmp_pembayaran_transaksi_penjualan",JSON.stringify(hasil));
  }

  errorhandling(error) {
    console.log(error);
    this.setState({
      isLoading: false,
    });
    ToastNotification("info", "Transaksi Gagal Disimpan");
  }
  ShowModalBarang() {
    this.props.dispatch(showModal());
    this.setState({ FormModal: "ShowModalBarang" });
  }
  ShowModalCustomer() {
    this.setState({ FormModal: "ShowModalCustomer" });
    this.props.dispatch(showModal());
  }
  showModalPembayaran() {
    // document.getElementById('jenis_pembayaran').focus();
    let customer =
      JSON.parse(localStorage.getItem("data_customer_services_tmp")) || [];
    let barang = JSON.parse(localStorage.getItem("data_barang_services")) || [];
    if (customer.length === 0 || barang.length === 0) {
      ToastNotification(
        "info",
        "Data Customer Atau Data Barang Belum Diisi !!!"
      );
    } else {
      this.setState({ FormModal: "ShowModalPembayaran" });
      this.props.dispatch(showModal());
    }
  }
  batalServices() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      localStorage.setItem("data_customer_services_tmp", JSON.stringify([]));
      this.props.dispatch(setTransaksiServices([]));
      localStorage.setItem("data_barang_services", JSON.stringify([]));
      this.setState({
        isLoadingBatal: false,
      });
    }, 500);
  }
  render() {
    return (
      <Panel>
          <textarea
            style={{ display: "none" }}
            id="myInput"
            rows="100"
            cols="100"
          />
        <div className="row">
          <div className="col-12">
            <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={
                this.props.setTransaksiServices.length === 0
                  ? JSON.parse(localStorage.getItem("data_barang_services")) ||
                    []
                  : this.props.setTransaksiServices || []
              }
              tambahData={true}
              btnText="Tambah Data Customer"
              columns={this.props.columns}
              btnOptional={true}
              namaCustomer={true}
              jenisCustomer={true}
              form="Service"
              btnTextOptional="Tambah Data Barang"
              handleClick={() => this.ShowModalCustomer()}
              handleClickOptional={() => this.ShowModalBarang()}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
        </div>

        <ModalGlobal
          size={this.state.FormModal ==="ShowModalCustomer" ? "xl" : this.state.FormModal === "ShowModalBarang" ? "L" : "P"}
          title={<TitleModal state={this.state.FormModal} />}
          content={
            this.state.FormModal === "ShowModalCustomer" ? (
              <ModalDataCustomer
                isLoadingDataCustomer={this.state.isLoadingDataCustomer}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            ) : this.state.FormModal === "ShowModalBarang" ? (
              <ModalTambahBarang onSubmit={(data) => this.simpanBarang(data)} />
            ) : (
              <ModalPembayaran
                isLoading={this.state.isLoading}
                bayarSekarang={(data) => this.bayarSekarang(data)}

              />
            )
          }
        />
        <div className="row">
          <div className="col-4">
            <button
              onClick={() => this.showModalPembayaran()}
              className="btn btn-primary"
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data Services"
              )}
            </button>
            &nbsp;
            <button
              onClick={() => this.batalServices()}
              className="btn btn-warning"
              disabled={this.state.isLoadingBatal}
              type="button"
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Reset
                </>
              ) : (
                "Reset"
              )}
            </button>
          </div>
        </div>
      </Panel>
    );
  }
}

function TitleModal(data, onSubmit) {
  // console.log(state.state);
  if (data.state === "ShowModalCustomer") {
    return "Form Data Customer";
  } else if (data.state === "ShowModalBarang") {
    return "Form Data Barang";
  } else {
    return "Form Pembayaran";
  }
}

HeadServices = reduxForm({
  form: "HeadServices",
  enableReinitialize: true,
  validate: ValidasiDataUsers,
})(HeadServices);
export default connect(maptostate, null)(HeadServices);

